import React, { Component } from 'react'
import Highlight from 'react-highlight';

require('highlight.js/lib/languages/shell');


import '../less/styles.less';

export default class extends Component {
  render() {
    return (
      <div id='Surface'>
        <div className='brand'>LCOMMIT</div>
        <Highlight className='github' languages={['shell']}>
          {`
            $ npm install -g lcommit
            
            $ lcommit --am "Hello There"
          `}
        </Highlight>
        <a href='https://github.com/yasaricli/lcommit.git'>
          <img src={require('../static/images/github.png')} />
        </a>
      </div>
    )
  }
}



